<template>
  <section>
    <ul>
      <li v-for="item in items" :key="item.id">
        <router-link :to="{ name: 'ItemView', params: { id: item.meta.id } }">{{
          item.name
        }}</router-link>
      </li>
    </ul>
  </section>
</template>

<script>
import itemService from "@/service/item.service.js";

export default {
  data() {
    return {
      items: [
        {
          name: String,
          id: Number,
        },
      ],
    };
  },
  mounted() {
    this.items = itemService.findAll();
  },
};
</script>